import { createStore } from 'vuex';

export default createStore({
  state: {
    tasks: localStorage.getItem('tasks')
      ? JSON.parse(localStorage.getItem('tasks'))
      : [],
    task: '',
    allTasksCompleted: true,
  },
  actions: {
    addTask({ commit, task }) {
      //don't add empty task
      if (this.state.task !== '') {
        commit('addTask', task);
      }
    },
    saveEditedTask({ commit, task }) {
      //don't add empty task
      if (this.state.task !== '') {
        commit('saveEditedTask', task);
      }
    },
    toggleAll({ commit }, id) {
      commit('toggleAll', id);
    },
    deleteTask({ commit }, id) {
      commit('deleteTask', id);
    },
    deleteCompletedTasks({ commit }) {
      commit('deleteCompletedTasks');
    },
  },

  mutations: {
    addTask(state) {
      const newTask = {
        id: state.tasks.length + 1,
        title: state.task,
        completed: false,
        editing: false,
      };
      state.tasks.push(newTask);
      localStorage.setItem('tasks', JSON.stringify(state.tasks));
    },
    saveEditedTask(state) {
      localStorage.setItem('tasks', JSON.stringify(state.tasks));
    },
    deleteTask(state, id) {
      state.tasks = state.tasks.filter((task) => task.id !== id);
      localStorage.setItem('tasks', JSON.stringify(state.tasks));
    },
    deleteCompletedTasks(state) {
      state.tasks = state.tasks.filter((task) => task.completed === true);
      localStorage.setItem('tasks', JSON.stringify(state.tasks));
    },
    toggleAll(state) {
      state.allTasksCompleted = !state.allTasksCompleted;
      state.tasks.forEach((task) => {
        task.completed = state.allTasksCompleted;
      });
      localStorage.setItem('tasks', JSON.stringify(state.tasks));
    },
  },

  modules: {},
});
