<template>
  <div class="todoapp">
    <todo-list-header />
    <todo-list-main v-if="areTasks" />
    <todo-list-footer v-if="areTasks" />
  </div>
</template>

<script>
import todoListHeader from '@/components/todoListComponents/todoListHeader.vue';
import todoListMain from '@/components/todoListComponents/todoListMain.vue';
import todoListFooter from '@/components/todoListComponents/todoListFooter.vue';

export default {
  name: 'todoList',
  components: {
    todoListHeader,
    todoListMain,
    todoListFooter,
  },
  computed: {
    tasks() {
      return this.$store.state.tasks;
    },
    areTasks() {
      return this.tasks.length > 0;
    },
  },
};
</script>
