<template>
  <main class="main">
    <input
      type="checkbox"
      class="toggle-all"
      id="toggle-all"
      v-model="allTasksCompleted"
      @change="toggleAll"
    />
    <label for="toggle-all"></label>
    <ul class="todo-list">
      <router-view />
    </ul>
  </main>
</template>

<script>
export default {
  name: 'todoListMain',
  data() {
    return {
      allTasksCompleted: false,
    };
  },
  computed: {
    tasks() {
      return this.$store.state.tasks;
    },
  },
  methods: {
    toggleAll() {
      this.$store.commit('toggleAll');
      this.$store.state.allTasksCompleted === false;
    },
  },
  mounted() {
    this.toggleAll();
  },
};
</script>

<style scoped>
.main {
  position: relative;
  z-index: 2;
  border-top: 1px solid #e6e6e6;
}

.toggle-all {
  text-align: center;
  border: none; /* Mobile Safari */
  opacity: 0;
  position: absolute;
}

.toggle-all + label {
  width: 60px;
  height: 34px;
  font-size: 0;
  position: absolute;
  top: -52px;
  left: -13px;
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
}

.toggle-all + label:before {
  content: '❯';
  font-size: 22px;
  color: #e6e6e6;
  padding: 10px 27px 10px 27px;
}

.toggle-all:checked + label:before {
  color: #737373;
}
</style>
